html {
  font-size: 16px;
}

h1,
h2,
p {
  margin: 0;
}

.regular {
  font-weight: 500;
  font-style: normal;
  font-family: "Red Hat Display", sans-serif;
}

.bold {
  font-weight: 700;
  font-style: normal;
  font-family: "Red Hat Display", sans-serif;
}

.black {
  font-weight: 900;
  font-style: normal;
  font-family: "Red Hat Display", sans-serif;
}
.black--att {
  text-decoration: none;
  font-family: "Azeret Mono", monospace;
}

* {
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}
.flex--column {
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
.flex--row {
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.flex--main {
  flex-flow: column nowrap;
  gap: 1.4375rem;
  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 519px) {
  .flex--main {
    gap: 0.9375rem;
  }
}
.flex--buttons {
  flex-flow: column nowrap;
  gap: 0.9375rem;
  justify-content: center;
  align-items: center;
}

.main {
  height: 29.8125rem;
}

.border--1 {
  border: solid yellow;
}
.border--2 {
  border: solid red;
}

.button {
  width: 100%;
  height: 3.125rem;
  border-radius: 1rem;
  border: none;
  font-size: 0.9375rem;
}
@media (max-width: 519px) {
  .button {
    width: 279px;
  }
}
.button:hover {
  cursor: pointer;
}
.button--main {
  box-shadow: 0 1rem 1.5rem -0.9rem #7280a7;
  background-color: #3829e0;
  color: white;
  transition: all 300ms ease-in-out;
}
.button--main:active {
  cursor: pointer;
  background-color: #7280a7;
}
.button--main:hover {
  box-shadow: 0 1rem 1.5rem -0.2rem #7280a7;
  border: solid 1px #1f2f56;
  transform: scale(1.02);
}
.button--alt {
  background-color: #fff;
  background-color: inherit;
  color: #7280a7;
}
.button--alt:hover {
  text-decoration: underline #1f2f56;
}
.button--alt:active {
  cursor: pointer;
  color: #1f2f56;
}

.card {
  margin: auto auto;
  width: 450px;
  height: 697px;
  border-radius: 1rem 1rem 1rem 1rem;
  background-color: white;
  margin-top: 6.4375rem;
  margin-bottom: 6.4375rem;
  box-shadow: 0 1.5rem 3rem -2.5rem #7280a7;
}
@media (max-width: 519px) {
  .card {
    width: 20.4375rem;
    height: 35.4375rem;
  }
}
.card__hero {
  border-radius: 1rem 1rem 0 0;
  width: auto;
  height: 13.75rem;
}
@media (max-width: 519px) {
  .card__hero {
    width: auto;
    height: 10rem;
  }
}
.card__title {
  color: #1f2f56;
}
.card__title--normal {
  font-size: 1rem;
}
@media (max-width: 519px) {
  .card__title--normal {
    font-size: 0.9375rem;
  }
}
.card__title--big {
  font-size: 1.8125rem;
  padding-bottom: 1rem;
}
@media (max-width: 519px) {
  .card__title--big {
    font-size: 1.3125rem;
  }
}
.card__paragraph {
  font-size: 1rem;
  line-height: 1.5rem;
  color: #7280a7;
}
@media (max-width: 519px) {
  .card__paragraph {
    font-size: 0.9375rem;
  }
}
.card__paragraph--center {
  text-align: center;
}
@media (max-width: 519px) {
  .card__paragraph--center {
    font-size: 1rem;
  }
}
.card__link {
  color: #3829e0;
}
.card__link:active {
  color: #7280a7;
}
@media (max-width: 519px) {
  .card__link {
    font-size: 0.8125rem;
  }
}
.card__plan {
  background-color: #f5f7ff;
  border-radius: 1rem;
}
@media (max-width: 519px) {
  .card__plan {
    width: 72.4%;
  }
}
.card__icon {
  border-radius: 100%;
  background-color: #e0e8ff;
}
.card__info {
  height: 100%;
}

.plan__container {
  width: 14.8125rem;
  height: 100%;
}
@media (max-width: 519px) {
  .plan__container {
    width: 72.4%;
  }
}

.attribution {
  font-family: "Azeret Mono", monospace;
  font-size: 0.625rem;
  text-align: center;
  width: 100vw;
}
.attribution__link {
  color: #660075;
}

.body {
  background-color: #e0e8ff;
  background-image: url("https://dndryn.github.io/order-summary-component-main/images/pattern-background-desktop.svg");
  background-position: top left;
  background-size: contain;
  background-repeat: no-repeat;
}
@media (max-width: 519px) {
  .body {
    width: 100vw;
  }
}

.content {
  margin: auto auto;
  width: 18.3125rem;
  height: 7.5rem;
  text-align: center;
}
.content--plan {
  width: 19.0625rem;
  height: 3rem;
  border-radius: 1rem;
}
@media (max-width: 519px) {
  .content--plan {
    width: 15.375rem;
  }
}
.content--main {
  width: 18.3125rem;
  height: 7.5rem;
}
@media (max-width: 519px) {
  .content--main {
    width: 17.4375rem;
  }
}

.container--main {
  width: 22.125rem;
  height: 7.5rem;
  justify-content: center;
  padding-top: 2.8125rem;
  padding-bottom: 0.625rem;
}
@media (max-width: 519px) {
  .container--main {
    padding-top: 1.875rem;
    padding-bottom: 0.625rem;
  }
}
.container--plan {
  width: 22.125rem;
  height: 6.25rem;
}
@media (max-width: 519px) {
  .container--plan {
    width: 17.4375rem;
    height: 5rem;
  }
}
.container--buttons {
  width: 22.125rem;
  padding-top: 0.875rem;
}
.container--att {
  width: 22.125rem;
  height: auto;
  margin: 2.8125rem auto 0.625rem auto;
}